define('tiny/components/subscription-form', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var loaded = false;

  exports['default'] = Ember['default'].Component.extend({
    trigger: (function () {
      if ($('#subscription-form').length == 1 && !loaded) {
        loaded = true;
        $('#subscription-form').steps({
          headerTag: 'h3',
          bodyTag: 'Section',
          transitionEffect: 'slideLeft',
          autoFocus: true
        });
      }
    }).on('didInsertElement')
  });

});