define('tiny/routes/admin/courses/edit', ['exports'], function (exports) {

  'use strict';

  exports['default'] = Ember.Route.extend({
    model: function model(params) {
      return this.store.findRecord('course', params.id);
    },
    actions: {
      saved: function saved(course) {
        course.save();
      }
    }
  });

});