define('tiny/routes/admin/courses/new', ['exports'], function (exports) {

  'use strict';

  exports['default'] = Ember.Route.extend({
    model: function model() {
      return {};
    },
    actions: {
      saved: function saved(data) {
        var course = this.get('store').createRecord('course', {
          name: data.name
        });
        course.save();
      }
    }
  });

});